const ADD_MESSAGE = 'ADD_MESSAGE';

const initialState = {
    dialogs: [
        {id: 1, name: ''},
        {id: 2, name: ''},
        {id: 3, name: ''}
    ],
    messages: [
        {id: 1, message:'Hi', author: 'You'},
        {id: 2, message: 'Hi-hi', author: 'Me'},
        {id: 3, message: 'How is your it-kamasutra', author: 'You'}
    ]
};

export default (state = initialState, action) => {

    switch(action.type) {
        case ADD_MESSAGE:
            return {
                ...state,
                messages: [
                    ...state.messages,
                    {
                        id: state.messages.length + 1,
                        author: 'Me',
                        message: action.data.message
                    }
                ]
            };

        default: break;
    }

    return state;
};

export const addMessage = data => ({type: ADD_MESSAGE, data});
