export const updateObjectInArray = (arr, obj) => {
    return arr.map( o => {
        return Number(o.id) === Number(obj.id) ? obj: o;
    })
};

export const updateObjectPropInArray = (arr, key, keyName, obj) =>
    arr.map( o => o[keyName] === key
        ? {...o, ...obj}
        : o
    );

export const objectToQueryString = obj => {
    const arr = [];
    Object.keys(obj).forEach(key => {
        if(obj[key] !== '') {
            arr.push(key + '=' + obj[key]);
        }
    });
    return arr.join('&');
};
