import {authAPI} from '../api/api'
import { DATA_LOADING, DATA_LOADED } from "./constants";
import {stopSubmit} from "redux-form";

const SET_CAPTCHA = 'SET_CAPTCHA';
const SET_AUTH_DATA = 'SET_AUTH_DATA';
const SET_AUTH_DATA_LOGOUT = 'SET_AUTH_DATA_LOGOUT';

const initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    loading: false,
    captcha: null
};

export default (state = initialState, action) => {
    switch(action.type) {
        case SET_CAPTCHA:
            return {
                ...state,
                captcha: action.captcha
            };

        case SET_AUTH_DATA:
            console.log(SET_AUTH_DATA);
            return {
                ...state,
                ...action.data,
                isAuth: true,
                captcha: null
            };

        case SET_AUTH_DATA_LOGOUT:
            console.log(SET_AUTH_DATA_LOGOUT);
            return {
                ...state,
                id: null,
                login: null,
                email: null,
                isAuth: false,
                captcha: null
            };

        case DATA_LOADING:
            return {
                ...state,
                loading: true
            };

        case DATA_LOADED:
            return {
                ...state,
                loading: false
            };

        default: break;
    }

    return state;
};

export const setCaptcha = captcha => ({type: SET_CAPTCHA, captcha});
export const setAuthData = data => ({type: SET_AUTH_DATA, data});
export const setAuthDataLogout = () => ({type: SET_AUTH_DATA_LOGOUT});
export const dataLoading = () => ({type: DATA_LOADING});
export const dataLoaded = () => ({type: DATA_LOADED});

export const authMe = () => async dispatch => {
    const {resultCode, data} = await authAPI.authMe();
    if (resultCode === 0) dispatch(setAuthData(data));
};

export const authLogin = data => async dispatch => {
    dispatch(dataLoading());
    const {resultCode, messages} = await authAPI.authLogin(data);
    if (!resultCode) {
        dispatch(authMe());
    }
    else {
        let errors = {};
        if(resultCode === 1) {
            errors = {
                _error: {
                    message: messages[0]
                },
                email: messages[0],
                password: messages[0]
            };
            dispatch(stopSubmit('login', errors));
        }
        if(resultCode === 10) {
            if(messages[0] === 'Incorrect Email or Password') {
                errors.email = messages[0];
                errors.password = messages[0];
            }
            else {
                errors.captcha = messages[0];
            }
            dispatch(authGetCaptchaUrl(errors));
        }
    }
    dispatch(dataLoaded());
};

export const authGetCaptchaUrl = errors => async dispatch => {
    dispatch(dataLoading());
    const {url} = await authAPI.authGetCaptchaUrl();
    dispatch(setCaptcha(url));
    dispatch(stopSubmit('login', errors));
    dispatch(dataLoaded());
};

export const authLogout = () => async dispatch => {
    dispatch(dataLoading());
    const {resultCode} = await authAPI.authLogout();
    if (resultCode === 0) dispatch(setAuthDataLogout());
    dispatch(dataLoaded());
};
