import * as axios from 'axios';
import {objectToQueryString} from '../utils/helpers/object'

const API_URL = 'https://social-network.samuraijs.com/api/1.0/';
const API_KEY = '48a0ebba-60b1-40b4-b189-f409d5d1ad7b';

const instance = axios.create({
    withCredentials: true,
    baseURL: API_URL,
    headers: {
        'API-KEY': API_KEY
    }
});


// Users
const findUsers = params => {
    let search = '?'+objectToQueryString(params);
    return instance.get(`users${search}`).then(r => r.data)
};
const postFollow = userId => {
    return instance.post(`follow/${userId}`).then(r => r.data)
};
const deleteFollow = userId => {
    return instance.delete(`follow/${userId}`).then(r => r.data)
};
export const userAPI = {
    // getUsers,
    findUsers,
    postFollow,
    deleteFollow
};

// Profile
const getProfile = userId => {
    return instance.get(`profile/${userId}`).then(r => r.data)
};
const getProfileStatus = userId => {
    return instance.get(`profile/status/${userId}`).then(r => r.data)
};
const updateProfileStatus = status => {
    return instance.put(`profile/status`, {
        status
    }).then(r => r.data)
};
const updateProfileData = data => {
    return instance.put(`profile`, data).then(r => r.data)
};
const uploadProfilePhoto = data => {
    return axios.post(`profile/photo`, data, {
        withCredentials: true,
        baseURL: API_URL,
        headers: {
            'API-KEY': API_KEY
        }
    }).then(r => r.data)
};
export const profileAPI = {
    getProfile,
    getProfileStatus,
    updateProfileStatus,
    uploadProfilePhoto,
    updateProfileData
};

// Auth
const authMe = () => {
    return instance.get(`auth/me`).then(r => r.data)
};
const authLogin = data => {
    return instance.post(`auth/login`, data).then(r => r.data)
};
const authLogout = () => {
    return instance.post(`auth/logout`).then(r => r.data)
};
const authGetCaptchaUrl = () => {
    return instance.get(`security/get-captcha-url`).then(r => r.data)
};
export const authAPI = {
    authMe, authLogin, authLogout, authGetCaptchaUrl
};
