import {userAPI} from "../../api/api";
import { DATA_LOADING, DATA_LOADED, LIMIT } from "../constants";
import {updateObjectPropInArray} from "../../utils/helpers/object";

const FOLLOW = 'FOLLOW';
const SET_USERS_DATA = 'SET_USERS_DATA';
const SET_SEARCH_DATA = 'SET_SEARCH_DATA';
const TOGGLE_IS_FOLLOWING = 'TOGGLE_IS_FOLLOWING';

const initialState = {
    users: [],
    friends: [],
    followingInProgress: [],
    loading: true,
    search: {
        term: '',
        count: LIMIT,
        page: 1,
        total: null
    }
};

export default (state = initialState, action) => {
    switch(action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjectPropInArray(state.users, action.id, 'id',{'followed': action.followed})
            };

        case TOGGLE_IS_FOLLOWING:
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.id]
                    : state.followingInProgress.filter(id => id !== action.id)
            };

        case SET_USERS_DATA:
            return {
                ...state,
                users: action.data,
                total: action.total,
                search: {
                    ...state.search,
                    total: action.total
                }
            };

        case SET_SEARCH_DATA:
            return {
                ...state,
                search: {
                    ...state.search,
                    ...action.data
                }
            };

        case DATA_LOADING:
            return {
                ...state,
                loading: true
            };

        case DATA_LOADED:
            return {
                ...state,
                loading: false
            };

        default: break;
    }

    return state;
};

export const follow = (id, followed) => ({type: FOLLOW, id, followed});
export const toggleIsFollowingInProgress = (id, isFetching) => ({type: TOGGLE_IS_FOLLOWING, id, isFetching});
export const setSearchData = data => ({type: SET_SEARCH_DATA, data});
export const setData = (data, total) => ({type: SET_USERS_DATA, data, total});

export const dataLoading = () => ({type: DATA_LOADING});
export const dataLoaded = () => ({type: DATA_LOADED});

export const fetchUsers = data => async dispatch => {
    dispatch(dataLoading());
    const {items, totalCount} = await userAPI.findUsers(data);
    dispatch(setData(items, totalCount));
    dispatch(dataLoaded());
};

export const updateSearchData = data => dispatch => {
    dispatch(setSearchData(data));
};

export const toggleFollow = (userId, followed) => async dispatch => {
    dispatch(toggleIsFollowingInProgress(userId, true));

    const {resultCode} = await userAPI[followed? 'deleteFollow': 'postFollow'](userId);
    if(!resultCode) dispatch(follow(userId, !followed));

    dispatch(toggleIsFollowingInProgress(userId, false));
};
