import {profileAPI} from "../../api/api";
import { DATA_LOADING, DATA_LOADED } from "../constants";
import {stopSubmit} from "redux-form";
import { lowerize } from '../../utils/helpers/capitalize';

const ADD_POST = 'ADD_POST';
const DELETE_POST = 'DELETE_POST';
const UPDATE_POST = 'UPDATE_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const UNSET_USER_PROFILE = 'UNSET_USER_PROFILE';
const SET_USER_PROFILE_STATUS = 'SET_USER_PROFILE_STATUS';
const SET_USER_PROFILE_PHOTO = 'SET_USER_PROFILE_PHOTO';

const defaulStatus = '  ';

const initialState = {
    posts: [
        {id: 1, title: 'Title of Post 1', content: 'Hi, how are you?', likesCount: 12},
        {id: 2, title: 'Title of Post 2', content: 'Hi, where are you?', likesCount: 11},
    ],
    profile: null,
    status: null,
    // loading: true,
};

export default (state = initialState, action) => {
    switch(action.type) {
        case ADD_POST:
            return {
                ...state,
                posts: [
                    ...state.posts,
                    {
                        id: state.posts.length + 1,
                        title: action.data.title,
                        content: action.data.content,
                        likesCount: 0
                    }
                ]
            };

        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(post => post.id !== action.id)
            };

        case UPDATE_POST:
            return {
                ...state,
                posts: [
                    ...state.posts.map(post => {
                        return post.id === action.data.id ? action.data: post;
                    })
                ]
            };

        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.data,
                status: action.status || defaulStatus,
            };

        case UNSET_USER_PROFILE:
            return {
                ...state,
                profile: null,
                status: null,
            };

        case SET_USER_PROFILE_PHOTO:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    photos: action.data
                }
            };

        case SET_USER_PROFILE_STATUS:
            return {
                ...state,
                status: action.data || defaulStatus
            };

        case DATA_LOADING:
            return {
                ...state,
                loading: true
            };

        case DATA_LOADED:
            return {
                ...state,
                loading: false
            };

        default: return state;
    }
};

export const addPost = data => ({type: ADD_POST, data});
export const deletePost = id => ({type: DELETE_POST, id});
export const updatePost = data => ({type: UPDATE_POST, data});
export const setUserProfile = (data, status) => ({type: SET_USER_PROFILE, data, status});
export const unsetUserProfile = () => ({type: UNSET_USER_PROFILE});
export const setUserProfilePhoto = data => ({type: SET_USER_PROFILE_PHOTO, data});
export const setProfileStatus = data => ({type: SET_USER_PROFILE_STATUS, data});

export const getProfile = (userId) => dispatch => {
    dispatch(unsetUserProfile());
    Promise.all([
        profileAPI.getProfile(userId),
        profileAPI.getProfileStatus(userId)
    ]).then(([data, status]) => {
        dispatch(setUserProfile(data, status))
    });
};

export const updateProfileStatus = status => dispatch => {
    profileAPI.updateProfileStatus(status).then(({resultCode}) => {
        if(!resultCode) dispatch(setProfileStatus(status));
    })
};

export const uploadProfilePhoto = data => dispatch => {
    profileAPI.uploadProfilePhoto(data).then(({resultCode, data: {photos}}) => {
        if(!resultCode) dispatch(setUserProfilePhoto(photos));
    })
};

export const updateProfileData = data => dispatch => {
    profileAPI.updateProfileData(data).then(({resultCode, messages}) => {
        if(!resultCode) dispatch(getProfile(data.userId));
        if(resultCode === 1) {
            let errors = {contacts: {}};
            messages.forEach( m => {
                const contactsError = m.match(/([\w ]*)+( \(Contacts->)+(\w*)/);
                if(contactsError[3]) {
                    errors.contacts[lowerize(contactsError[3])] = contactsError[1];
                }
            });
            dispatch(stopSubmit('update-profile-form', {
                ...errors
            }));
        }
    })
};
