import {applyMiddleware, combineReducers, createStore, compose} from 'redux';
import thunkMiddleware from 'redux-thunk';
import profileReducer from "./profile/reducer";
import dialogsReducer from "./dialogsReducer";
import sidebarReducer from "./sidebarReducer";
import usersReducer from "./users/reducer";
import loadingReducer from "./loadingReducer";
import authReducer from "./authReducer";
import { reducer as formReducer } from 'redux-form';
import appReducer from "./appReducer";

const reducers = combineReducers({
    appData: appReducer,
    authData: authReducer,
    loadingData: loadingReducer,
    usersData: usersReducer,
    profilePageData: profileReducer,
    dialogsPageData: dialogsReducer,
    sidebarData: sidebarReducer,
    form: formReducer
});

const persistedState = localStorage.getItem('appAuth')
    ? { authData: JSON.parse(localStorage.getItem('appAuth')) }
    : {};

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, persistedState, composeEnhancers(
    applyMiddleware(thunkMiddleware)
));

// store.subscribe(() => {
//     localStorage.setItem('appAuth', JSON.stringify(store.getState().authData))
// });

export default store;
