import { DATA_LOADING, DATA_LOADED } from "./constants";

const initialState = {
    loading: true
};

export default (state = initialState, action) => {
    switch(action.type) {
        case DATA_LOADING:
            return {
                ...state,
                loading: true
            };

        case DATA_LOADED:
            return {
                ...state,
                loading: false
            };

        default: break;
    }

    return state;
};

export const dataLoading = () => ({type: DATA_LOADING});
export const dataLoaded = () => ({type: DATA_LOADED});
