import React, {useEffect, Suspense } from 'react';
import {HashRouter, Route, withRouter} from 'react-router-dom';

import {compose} from "redux";
import {connect, Provider} from "react-redux";
import {initializeApp} from "./redux/appReducer";
import store from "./redux/redux-store";

import './App.scss';
import { ReactComponent as Sprite } from './assets/images/sprite.svg';
import Preloader from "./components/common/Preloader";
import HeaderContainer from './components/Header/container';
import Sidebar from './components/Sidebar';

const LoginContainer = React.lazy(() => import('./components/Login/container'));

const Profile = React.lazy(() => import('./components/Profile'));
const UsersContainer = React.lazy(() => import('./components/Users/container'));
const DialogsContainer = React.lazy(() => import('./components/Dialogs/container'));
const FriendsContainer = React.lazy(() => import('./components/Friends/container'));
const FriendContainer = React.lazy(() => import('./components/Friend/container'));

const News = React.lazy(() => import('./components/News/News'));
const Music = React.lazy(() => import('./components/Music/Music'));
const Settings = React.lazy(() => import('./components/Settings'));

const App = ({initialized, initializeApp}) => {

    useEffect(
        () => {
            if(!initialized) initializeApp()
        },
        [initialized, initializeApp]
    );

    return (
        <div className="app-wrapper">
            <HeaderContainer/>
            <div className="app-sidebar">
                <Sidebar/>
            </div>
            <div className="app-content">
                <Suspense fallback={<Preloader loading={true}/>}>
                {!initialized
                ? <Preloader loading={true}/>
                : <>
                    <Route exact path="/" component={Profile}/>
                    <Route path="/profile/:userId?" component={Profile}/>
                    <Route exact path="/messages" component={DialogsContainer}/>
                    <Route path="/messages/:id" component={DialogsContainer}/>
                    <Route exact path="/friends" component={FriendsContainer}/>
                    <Route path="/friends/:id" component={FriendContainer}/>
                    <Route path="/news" component={News}/>
                    <Route path="/music" component={Music}/>
                    <Route path="/settings" component={Settings}/>
                    <Route path="/users" component={UsersContainer}/>
                    <Route path="/login" component={LoginContainer}/>
                  </>
                }
                </Suspense>
            </div>
            <Sprite style={{display: 'none'}}/>
        </div>
    )
};

const AppCompose =  compose(
    withRouter,
    connect(s => s.appData, { initializeApp })
)(App);

export default () => {
    const baseName = process.env.NODE_ENV === "development"
        ? {}
        : {basename: process.env.PUBLIC_URL};

    return (
        <HashRouter {...baseName}>
            <Provider store={store}>
                <AppCompose />
            </Provider>
        </HashRouter>
    );
}

