import { createSelector } from 'reselect';

export const getUsers = state => {
    return state.usersData.users
};
export const getFriends = createSelector(
    [getUsers],
    users => users.filter(u => u.followed)
);

export const getSearchData = state => {
    return state.usersData.search
};

export const getPage = state => {
    return state.usersData.page
};

export const getTotalUsersCount = state => {
    return state.usersData.total
};

export const getFollowingInProgress = state => {
    return state.usersData.followingInProgress
};

export const getLoading = state => {
    return state.usersData.loading
};

