import React from 'react';
import s from './Friends.module.scss';
import {Link} from 'react-router-dom';
import defaultAva from '../../../assets/images/logo192.png'

export default ({friends}) => (
    <div className={s.Friends} key={'Friends'}>
        <Link to={'/friends'} className={s.title}>Friends</Link>
        <ul className={s.list}>
            {friends.map( ({id, name, photos: {small}}) =>
                <li className={s.item} key={id}>
                    <Link to={'/profile/'+id}>
                        <img className={s.ava} src={small ? small: defaultAva} alt={name}/>
                        {name}
                    </Link>
                </li>
            )}
        </ul>
    </div>
);
