import React from 'react';
import s from './Header.module.scss';
import {NavLink} from "react-router-dom";
import Icon from '../common/Icon';

const Header = ({isAuth, id, login, authLogout}) => {
    return (
        <header className={s.header}>
            <NavLink to={'/'} className={s.Logo}>
                <img src="https://image.flaticon.com/icons/svg/124/124024.svg" className={s.Logo_Img} alt="A make app"/>
            </NavLink>
            <div className={s.Login}>
                {isAuth
                    ? <>
                        <NavLink to={'/profile/' + id}>{login}</NavLink>
                        <button className={s.Logout} onClick={authLogout}>
                            <Icon className={s.LogoutIcon} icon={'logout'}/>
                        </button>
                    </>
                    : <NavLink to={'/login'}>Login</NavLink>
                }
            </div>
        </header>
    );
};

export default Header;
